<template>
  <div class="container mt-4">
    <div v-if="loading" class="text-center">
      <div class="spinner-border" role="status">
        <span class="visually-hidden">Chargement...</span>
      </div>
    </div>
    <div v-if="error" class="alert alert-danger">{{ error }}</div>
    <div v-if="catalog">
      <div class="d-flex justify-content-between align-items-center mb-3">
        <h1>{{ catalog.title }}</h1>
        <button class="btn btn-outline-primary" @click="toggleNoteMode">
          {{ isNoteMode ? 'Quitter le mode Notes' : 'Activer le mode Notes' }}
        </button>
      </div>
      <p>
        <a :href="getStrapiMediaUrl(catalog.pdf_file_path)" target="_blank">Voir le PDF</a> |
        <a :href="getStrapiMediaUrl(catalog.jpeg_preview_path)" target="_blank">Voir l'aper√ßu</a>
      </p>
      <hr />
      <h2>Pages</h2>
      <div class="catalog-container">
        <div v-for="(pair, index) in pairedPages" :key="index" class="page-pair">
          <!-- Page de gauche -->
          <div class="page left" v-if="pair[0]" @click="startNewNote($event, pair[0])">
            <div class="card-body">
              <div class="products-grid">
                <div v-for="product in pair[0].products" :key="product.id" class="product-item">
                  <img v-if="product.image"
                       :src="getStrapiMediaUrl(product.image.url)"
                       :alt="product.name"
                       class="product-image">
                  <strong>{{ product.name }}</strong>
                  <span>${{ product.price }}</span>
                  <p>{{ product.description }}</p>
                </div>
              </div>
            </div>
            <template v-if="isNoteMode">
              <div 
                v-for="note in pair[0].notes" 
                :key="note.documentId" 
                class="note-bubble" 
                :class="{ 'is-dragging': draggedNote && draggedNote.note.documentId === note.documentId }"
                :style="{ top: note.y + 'px', left: note.x + 'px' }"
                @mousedown.stop="startDrag($event, note)"
              >
                <button @click.stop="deleteNote(note)" class="delete-note-btn">√ó</button>
                <strong>{{ note.title }}</strong>
                <p>{{ note.content }}</p>
              </div>
              <div v-if="newNoteData && newNoteData.page === pair[0].id" class="new-note-form" :style="{ top: newNoteData.y + 'px', left: newNoteData.x + 'px' }">
                <input type="text" v-model="newNoteData.title" placeholder="Titre" class="form-control mb-2">
                <textarea v-model="newNoteData.content" placeholder="Votre note..." class="form-control mb-2"></textarea>
                <button @click.stop="submitNewNote" class="btn btn-sm btn-primary">Enregistrer</button>
                <button @click.stop="cancelNewNote" class="btn btn-sm btn-secondary">Annuler</button>
              </div>
            </template>
            <div class="page-number">{{ pair[0].page_number }}</div>
          </div>
          <div class="page-placeholder" v-else></div>

          <!-- Page de droite -->
          <div class="page right" v-if="pair[1]" @click="startNewNote($event, pair[1])">
            <div class="card-body">
               <div class="products-grid">
                <div v-for="product in pair[1].products" :key="product.id" class="product-item">
                  <img v-if="product.image"
                       :src="getStrapiMediaUrl(product.image.url)"
                       :alt="product.name"
                       class="product-image">
                  <strong>{{ product.name }}</strong>
                  <span>${{ product.price }}</span>
                  <p>{{ product.description }}</p>
                </div>
              </div>
            </div>
            <template v-if="isNoteMode">
              <div 
                v-for="note in pair[1].notes" 
                :key="note.documentId" 
                class="note-bubble" 
                :class="{ 'is-dragging': draggedNote && draggedNote.note.documentId === note.documentId }"
                :style="{ top: note.y + 'px', left: note.x + 'px' }"
                @mousedown.stop="startDrag($event, note)"
              >
                <button @click.stop="deleteNote(note)" class="delete-note-btn">√ó</button>
                <strong>{{ note.title }}</strong>
                <p>{{ note.content }}</p>
              </div>
              <div v-if="newNoteData && newNoteData.page === pair[1].id" class="new-note-form" :style="{ top: newNoteData.y + 'px', left: newNoteData.x + 'px' }">
                <input type="text" v-model="newNoteData.title" placeholder="Titre" class="form-control mb-2">
                <textarea v-model="newNoteData.content" placeholder="Votre note..." class="form-control mb-2"></textarea>
                <button @click.stop="submitNewNote" class="btn btn-sm btn-primary">Enregistrer</button>
                <button @click.stop="cancelNewNote" class="btn btn-sm btn-secondary">Annuler</button>
              </div>
            </template>
            <div class="page-number">{{ pair[1].page_number }}</div>
          </div>
          <div class="page-placeholder" v-else></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import strapiApi from '@/services/strapiApi';

export default {
  name: 'CatalogDetail',
  props: {
    id: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      catalog: null,
      loading: true,
      error: null,
      strapiBaseUrl: process.env.VUE_APP_STRAPI_URL,
      newNoteData: null,
      isNoteMode: false,
      notesLoaded: false,
      draggedNote: null, // For drag and drop
    };
  },
  computed: {
    pairedPages() {
      if (!this.catalog || !this.catalog.pages) {
        return [];
      }
      const pages = [...this.catalog.pages].sort((a, b) => a.page_number - b.page_number);
      const pairs = [];
      if (pages.length > 0) {
        pairs.push([null, pages[0]]);
      }
      for (let i = 1; i < pages.length; i += 2) {
        if (i + 1 < pages.length) {
          pairs.push([pages[i], pages[i + 1]]);
        } else {
          pairs.push([pages[i], null]);
        }
      }
      return pairs;
    },
  },
  async created() {
    this.fetchInitialData();
  },
  methods: {
    getStrapiMediaUrl(path) {
      if (!path) return '';
      return `${this.strapiBaseUrl}${path}`;
    },
    async fetchInitialData() {
      this.loading = true;
      try {
        const response = await strapiApi.getCatalog(this.id);
        this.catalog = response.data.data;
      } catch (err) {
        this.error = 'Erreur lors du chargement du catalogue.';
        console.error(err);
      } finally {
        this.loading = false;
      }
    },
    async toggleNoteMode() {
      this.isNoteMode = !this.isNoteMode;
      if (this.isNoteMode && !this.notesLoaded) {
        await this.fetchNotes();
      }
    },
    async fetchNotes() {
      this.loading = true;
      try {
        const response = await strapiApi.getCatalogWithNotes(this.id);
        this.catalog = response.data.data;
        this.notesLoaded = true;
      } catch (err) {
        this.error = 'Erreur lors du chargement des notes.';
        console.error(err);
      } finally {
        this.loading = false;
      }
    },
    startNewNote(event, page) {
      if (!this.isNoteMode || !page) return;
      if (this.newNoteData) return;

      if (event.target.closest('.note-bubble') || event.target.closest('.new-note-form')) {
          return;
      }

      const rect = event.currentTarget.getBoundingClientRect();
      this.newNoteData = {
        x: Math.round(event.clientX - rect.left),
        y: Math.round(event.clientY - rect.top),
        page: page.id,
        title: '',
        content: '',
      };
    },
    async submitNewNote() {
      if (!this.newNoteData || !this.newNoteData.content) {
        alert('Veuillez saisir un contenu pour la note.');
        return;
      }
      try {
        await strapiApi.createNote(this.newNoteData);
        await this.fetchNotes();
        this.newNoteData = null;
      } catch (err) {
        console.error("Erreur lors de l'ajout de la note:", err);
        alert("Erreur lors de l'ajout de la note. Veuillez r√©essayer.");
      }
    },
    cancelNewNote() {
      this.newNoteData = null;
    },
    async deleteNote(noteToDelete) {
      if (confirm('√ätes-vous s√ªr de vouloir supprimer cette note ?')) {
        try {
          await strapiApi.deleteNote(noteToDelete.documentId);
          await this.fetchNotes(); // Refresh notes
        } catch (err) {
          console.error('Erreur lors de la suppression de la note:', err);
          alert('Erreur lors de la suppression de la note.');
        }
      }
    },
    startDrag(event, note) {
      if (!this.isNoteMode) return;
      this.draggedNote = {
        note,
        initialX: note.x,
        initialY: note.y,
        offsetX: event.clientX - note.x,
        offsetY: event.clientY - note.y,
      };
      window.addEventListener('mousemove', this.handleDrag);
      window.addEventListener('mouseup', this.stopDrag);
    },
    handleDrag(event) {
      if (!this.draggedNote) return;
      event.preventDefault();
      const { note, offsetX, offsetY } = this.draggedNote;
      note.x = event.clientX - offsetX;
      note.y = event.clientY - offsetY;
    },
    async stopDrag() {
      if (!this.draggedNote) return;
      const { note, initialX, initialY } = this.draggedNote;
      window.removeEventListener('mousemove', this.handleDrag);
      window.removeEventListener('mouseup', this.stopDrag);

      if (note.x !== initialX || note.y !== initialY) {
        try {
          await strapiApi.updateNote(note.documentId, { x: note.x, y: note.y });
        } catch (err) {
          console.error('Erreur lors de la mise √† jour de la position de la note:', err);
          alert('Impossible de sauvegarder la nouvelle position de la note.');
          note.x = initialX;
          note.y = initialY;
        }
      }
      this.draggedNote = null;
    },
  },
};
</script>

